import * as Styled from './styles';
import {ArticleMeta} from '../ArticleMeta';

export type ArticleHeaderProps = {
  title: string;
  capa: string;
  articleMeta: ArticleMeta;
};

export const ArticleHeader = ({
  title,
  capa,
  articleMeta,
}: ArticleHeaderProps) => {
  return (
    <Styled.Wrapper>
      <h1>Oi</h1>
      <p>{title}</p>
    </Styled.Wrapper>
  );
};
